import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, EffectCoverflow } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import useAnimation from "../../hooks/useAnimation";
import "./Project.css";

import image1 from "../../assets/images/.png";
import image2 from "../../assets/images/2.png";
import image3 from "../../assets/images/.png";
import image4 from "../../assets/images/2.png";
import image5 from "../../assets/images/.png";

const cardData = [
    {
        title: "   (23. 5 ~ 23. 6)",
        description: `
         ,     

        #React.js, #Recoil, #Node.js, #MongoDB`,
        link: "",
        image: image1,
    },
    {
        title: "   (22. 12 ~ 23. 4)",
        description: `
                  
        
        #Angaulr2, #Rxjs, #Node.js, #MongoDB`,
        link: "",
        image: image2,
    },
    {
        title: "   (22. 10 ~ 22. 12)",
        description: `
              
        
        #React.js, #Recoil, #Node.js, #MongoDB`,
        link: "",
        image: image3,
    },
    {
        title: "  (21. 5 ~ 22. 9)",
        description: `
                  
        
        #Angaulr2, #Rxjs, #Node.js, #MongoDB`,
        link: "https://potatocs.com",
        image: image4,
    },
    {
        title: "  (23.5 ~ 23.6)",
        description: `
             

        #Next.js, #Recoil, #Node.js, #MongoDB`,
        link: "https://www.kimsangmin.com",
        image: image5,
    },
];

function Project() {
    const headingRef = useAnimation();

    const handleCardClick = (link) => {
        if (link) {
            window.open(link, "_blank", "noopener,noreferrer");
        }
    };

    return (
        <div className="projects_container">
            <div className="projects_title">
                <h1 className="heading" ref={headingRef}>
                    Projects
                    <span className="projects_highlight"></span>
                </h1>
            </div>
            <div className="projects_content">
                <Swiper
                    pagination={{
                        type: "progressbar",
                        className: "pagination",
                    }}
                    observer={true}
                    observeParents={true}
                    watchSlidesProgress={true}
                    slidesPerView={3} //  
                    spaceBetween={600} //   
                    slidesPerGroup={1} //   
                    centeredSlides={true} // 
                    coverflowEffect={{
                        rotate: 10, // 
                        stretch: 0,
                        depth: 100, // 
                        modifier: 2, //
                        slideShadows: true, //     .
                    }}
                    // slidesOffsetBefore={150}
                    // slidesOffsetAfter={150}
                    loop={true}
                    breakpoints={{
                        640: {
                            slidesPerView: 1,
                            spaceBetween: 10,
                        },
                        768: {
                            slidesPerView: 1.5,
                            spaceBetween: 20,
                        },
                        1024: {
                            slidesPerView: 1.8,
                            spaceBetween: 30,
                        },
                    }}
                    modules={[
                        Pagination,
                        Navigation,
                        EffectCoverflow,
                        Navigation,
                    ]}
                    className="mySwiper"
                >
                    {cardData.map((card, index) => (
                        <SwiperSlide key={index}>
                            <div
                                className={`card ${
                                    card.link ? "clickable" : ""
                                }`}
                                onClick={() => handleCardClick(card.link)}
                            >
                                <img
                                    src={card.image}
                                    alt={card.title}
                                    className="card_img"
                                />
                                <div className="card_title">{card.title}</div>
                                <div
                                    className="card_description"
                                    style={{
                                        whiteSpace: "pre-line",
                                        display: "flex",
                                        textAlign: "justify",
                                    }}
                                >
                                    {card.description}
                                </div>
                            </div>
                        </SwiperSlide>
                    ))}
                </Swiper>
            </div>
        </div>
    );
}

export default Project;
